:root {
  --primary-text: #000;
  --secondary-text: #907e82;
  --header-hero-text: #fff;
  --accent-color: #d41443;
  --pink-color: #ffa5ba;
  --primary-bcg: #fff;
  --secondary-bcg: #ffb8ca;
  --mob-menu-color: #e17992;
  --primary-btn: #fff;
  --secondary-btn-color: #d41443;
  --hover-btn-primary: #ecebeb;
  --hover-btn-secondary: #ffa5ba;
  --ice-color: #ffb8ca;
  --coffee-color: #f0d1a5;
  --shake-color: #c2e297;
  --pseudo-color: #e1e1e1;
  --primary-gradient: #fff5f6;
  --secondary-gradient: #fff;
  --outline: 1px solid tomato;
  --cubic: cubic-bezier(.4, 0, .2, 1);
  --social-color1: #fea5bb;
  --social-color2: #fff;
}

body {
  color: var(--primary-text);
  background-color: var(--primary-bcg);
  letter-spacing: .04em;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.75;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  padding: 0;
}

.link {
  cursor: pointer;
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.section {
  padding-top: 30px;
  padding-bottom: 30px;
}

@media screen and (min-width: 1200px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.section-heading {
  color: var(--accent-color);
  text-transform: uppercase;
  text-align: center;
  font-family: Titan One, cursive;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.15;
}

@media screen and (min-width: 768px) {
  .section-heading {
    font-size: 48px;
  }
}

@media screen and (min-width: 1200px) {
  .section-heading {
    letter-spacing: .06em;
    font-size: 58px;
    line-height: 1.14;
  }
}

.section-subtitle {
  color: var(--pink-color);
  text-transform: uppercase;
  text-align: center;
  font-family: Titan One, cursive;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.17;
}

@media screen and (min-width: 768px) {
